import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { CustomerReviewService } from 'src/app/service/customer-review.service';
declare var swal: any;

@Component({
  selector: 'app-customer-review-add',
  templateUrl: './customer-review-add.component.html',
})
export class CustomerReviewAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  paymentTypeList: any = [];
  showLoader = false;
  formErrors = {
    name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private customerreviewService: CustomerReviewService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      payment_type: [1],
      name: [null, Validators.compose([Validators.required])],
      email: [null, Validators.compose([Validators.required, Validators.email])],
      subject: [null, Validators.compose([Validators.required])],
      quality_rate: [2, Validators.compose([Validators.required])],
      price_rate: [3, Validators.compose([Validators.required])],
      value_rate: [4, Validators.compose([Validators.required])],
      massage: ['']
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.customerreviewService.getCustomerReviewById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            payment_type: response.data.payment_type,
            name: response.data.name,
            email: response.data.email,
            subject: response.data.subject,
            quality_rate: response.data.quality_rate,
            price_rate: response.data.price_rate,
            massage: response.data.massage
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/customer-review');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.customerreviewService.editCustomerReview(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/customer-review');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }

            });
          }
        });
      } else {
        this.customerreviewService.addCustomerReview(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/customer-review');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }
            });
          }
        });
      }
    }
  }
}
